.pagination {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: flex-end;
    margin: 20px 0;
    li{
        position: relative;
        &:first-child{
            i.icon-7-0_doublearrow{
                transform: rotate(180deg);
                left: 13px;
            }
        }
        &:nth-child(2){
            i.icon-1-7_down{
                transform: rotate(90deg);
                left: 13px;
            }
        }
        &:nth-last-child(2){
            i{
                transform: rotate(-90deg);
            }
        }

        &:not(:last-of-type){
            margin-right: 5px;
        }
        &:hover, &.active{
            a{
                border-color: #445f87;
                transition: all .3s ease-out;
                color: #445f87;
            }
        }
        a{
            width: 37px;
            height: 37px;
            text-decoration: none;
            color: #000000;
            border-radius: 10px;
            border: 1px solid #e2e2e2;
            font-size: 14px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all .3s ease-out;
        }
        i.icon-7-0_doublearrow{
            font-size: 10px;
            position: absolute;
            top: 14px;
            left: 15px;
        }
        i.icon-1-7_down{
            font-size: 6px;
            position: absolute;
            top: 16px;
            left: 15px;
        }
        &.left{
            margin-right: 20px;
        }
        &.right{
            margin-left: 15px;
        }
        &.disabled{
            z-index: -1;
            a, &:hover a{
                color: #e1e1e1;
                border-color: #e1e1e1;
            }

        }
    }
}
@media(max-width: 575px){
    .pagination{
        justify-content: center;
        margin: 10px 0;
        li{
            &.left{
                margin-right: 5px;
            }
            &.right{
                margin-left: 0;
            }
        }
    }
}